<template>
  <div id="app">
    <div class="header">
      <div>

        <ul class="header-button-left" v-if=" step != 0" @click=" step = 0">
          <li>Cancel</li>
        </ul>

        <ul class="header-button-right" v-if=" step == 1" @click="step = 2">
          <li>Next</li>
        </ul>
        <ul class="header-button-right" v-if=" step == 2" @click="publish">
          <li>발행</li>
        </ul>

      </div>

      <img src="./assets/logo.png" class="logo" />
    </div>

    <Body v-on:글입력="입력한글 = $event" v-bind:포스팅들="포스팅들" v-bind:step="step" v-bind:업로드이미지="업로드이미지" />

    <div class="footer">
      <ul class="footer-button-plus">
        <!-- <li>+</li> -->
        <input type="file" id="file" class="inputfile" v-on:change="upload" />
        <label for="file" class="input-plus">+</label>
      </ul>
    </div>
  </div>
</template>

<script>
import Body from "./components/Body";
import Data from "./assets/postdata.js";

export default {
  name: "App",
  data() {
    return {
      포스팅들: Data,
      //step이 0이면 post.vue / 1이면 필터선택화면 / 2이면 글작성화면
      step: 0,
      업로드이미지: '',
      입력한글 : '',
    };
  },
  methods: {
    upload(e) {
      var file = e.target.files;
      let reader = new FileReader();
      reader.readAsDataURL(file[0]);
      reader.onload = e => {
        this.업로드이미지 = e.target.result;
        this.step = 1;
        // console.log(this.업로드이미지);
      };
    },
    publish() {
      // 1. 메인 화면으로 돌아감
      this.step = 0;
      // 2. 내가 작성한글, 업로드이미지를 바탕으로 {} 만들기
      var 오브젝트 = {
        name: "Kimhyukmin",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.업로드이미지,
        likes: 36,
        date: 'May 15',
        liked: false,
        caption: this.입력한글,
        filter: "perpetua"
      };
      // 3. this. 포스팅들 맨앞에 추가함
      this.포스팅들.unshift(오브젝트);
    },
  },
  components: {
    Body: Body
  }
};
</script>

